require('@lib/js.fullpage')

export default {
  data() {
    return {};
  },
  methods: {
    onclickNoticeButton() {
      this.$router.push('dreamX');
    },
  },
  mounted() {
    document.getElementsByClassName('wp-inner')[0].fullpage({
      start: 0,
    });
  }
};



