import { downloadImage, previewImage, chooseImage, uploadImage, getLocalImgData } from '@utils/ydk';
import { filterWord } from './config'
import html2canvas from 'html2canvas';
import { mapState } from 'vuex'

export default {
  data() {
    return {
      img: '',
      imgUrl: '',

      showShare: false,
    };
  },
  computed: mapState([
    //  this.count  store.state.count
    'dreamObj', 'addressObj'
  ]),
  methods: {
    onShare() {
      this.showShare = !this.showShare;
    }
  },

  filters: {
    processWord: function (value, type) {
      if (!value) return '';
      filterWord.forEach((word) => {
        const reg = new RegExp(word, 'g');
        value = value.replace(reg, '*');
      });
      if (type === 'name') value = value.slice(0, 4);
      if (type === 'dream') value = value.slice(0, 60);
      return value;
    }
  },
  mounted: async function () {
    const host = `${location.origin}${location.pathname}`.split('#')[0].replace(/index(\d)?\.html/, '');
    this.imgUrl = `${host}/static/img/result-bg.jpg`;
    let imgHeight = window.document.querySelector('#target').offsetHeight // DOM
    let imgWidth = window.document.querySelector('#target').offsetWidth // DOM
    let scale = window.devicePixelRatio // 

    let name = '';
    // 
    filterWord.forEach((word) => {
      name = name.replace(word, '**');
    });
    // this.userName = name;
    const canvasR = await html2canvas(document.querySelector('#target'), {
      scale: scale,
      width: imgWidth,
      height: imgHeight
    });
    this.img = await canvasR.toDataURL("image/jpeg", 1.0);//base64
  }
};


